/*mobile navigation*/
.btn-menu {
  display: block;
  position: fixed;
  top: 110px;
  left: 80px;
  width: 58px;
  height: 58px;
  background-color: #fff;
  border: 1px solid #fff;
  cursor: pointer;

  z-index: 12;
  &:hover {
    border-color: #ee0e09;
  }
}

.main-top-section .btn-menu,
.btn-menu.red {
  background-color: #ee0e09;
  border-color: #5A5751;
}

.btn-menu span {
  display: block;
  background-color: #ee0e09;
  position: absolute;
  top: 29px;
  left: 19px;
  width: 20px;
  height: 2px;
  border: none;
  transform-origin: center center;
  transition: all .4s;
}

.btn-menu:hover span {
  left: 8px;
  width: 40px;
}

.main-top-section .btn-menu span,
.btn-menu.red span {
  background-color: #fff;
}

.btn-menu span > i {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  background-color: #ee0e09;
  transition: all .4s;
  transform-origin: center center;
}

.main-top-section .btn-menu span > i,
.btn-menu.red span > i {
  background-color: #fff;
}

.btn-menu span:before, 
.btn-menu span:after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  height: 2px;
  background-color: #ee0e09;
  transition: all .4s;
}


.main-top-section .btn-menu span:before,
.main-top-section .btn-menu span:after,
.btn-menu.red span:before, 
.btn-menu.red span:after {
  background-color: #fff;
}

.btn-menu span:before {
  top: -7px;
  width: 16px;
}

.btn-menu span:after {
  top: 7px;
  width: 12px;
}

.btn-menu:hover span:before, 
.btn-menu:hover span:after {
  width: 40px;
}


/*modal window*/
.modal-cont {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-height: 98vh;
  overflow-y: auto;
  width: 540px;
  max-width: 96%;
  padding: 55px 20px;
  background: #fff;
  z-index: 21;
}



.modal-logo {
  display: block;
  max-width: 120px;
}

.modal-form input[type="text"],
.modal-form input[type="tel"] {
  line-height: 62px;
  padding: 0 10px 0 64px;

}

.inline-form input[type="text"],
.modal-form input[type="text"] {
  background-image:url(../img/icons/user-i.png);
  background-position: 40px 50%;
  background-repeat: no-repeat;
}

.inline-form input[type="tel"],
.modal-form input[type="tel"] {
  background-image:url(../img/icons/phone-i.png);
  background-position: 40px 50%;
  background-repeat: no-repeat;
}


.modal-form input[type="text"],
.modal-form input[type="tel"],
.modal-form textarea {
  display: block;
  width: 100%;
  max-width: 350px;
  height: 66px;
  font-size: 18px;
  border: 2px solid #9D9D9D;


  transition: all .4s;
  margin: 0 auto 30px;
  border-radius: 33px;
  font-family: $med;
  font-size: 18px;
  color: $main;
  line-height: 1.1;
  &:focus {
    border-color: #ee0e09;
  }
}

.modal-form textarea {
  padding: 21px 10px 6px 64px;
  resize: vertical;
  min-height: 66px;

  &:focus,
  &.non-empty {


    height: 120px;
    max-height: 120px;
    padding-left: 10px;
    overflow: auto;
  }

}

.modal-form .button {
  display: block;
  margin: 0 auto;
}

.modal-close {
  font-size: 24px;
  line-height: 1;
  color: #ee0e09;
  position: absolute;
  top: 30px;
  right: 30px;
  cursor: pointer;
}

.modal-logo {
  display: block;
  margin: 0 auto 40px;
  width: 164px;
}


.modal-head {
  font-family: $black;
  font-size: 36px;
  margin-bottom: 25px;
  text-align: center;
}

.modal-subhead {
  font-family: $med;
  font-size: 18px;
  margin-bottom: 60px;
  text-align: center;
}


.inputs-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}

.inline-form {

  input[type="text"],
  input[type="tel"],
  textarea {
    flex: 1 1 100%;
    max-width: 31.5%;
    height: 68px;
    font-size: 18px;
    padding: 0 10px 0 65px;
    border-radius: 34px;
    border: 2px solid #9D9D9D;
    transition: all .4s;
    &:focus {
      border-color: $red;
    }
  }
  input[type="text"],
  input[type="tel"] {
    line-height: 64px;
  }
  textarea {
    min-height: 68px;
    max-height: 200px;
    line-height: 1.1;
    padding-top: 23px;
    &:focus,
    &.non-empty {
      height: 150px;
      padding: 8px 10px;
      
    }
  }
  ::-webkit-input-placeholder {
    font-size: 18px;
    color: #A4A4A4;
  }

  ::-moz-placeholder {
    font-size: 18px;
    color: #A4A4A4;
  }

  :-ms-input-placeholder {
    font-size: 18px;
    color: #A4A4A4;
  }

  ::placeholder {
    font-size: 18px;
    color: #A4A4A4;
  }
  .button {
    display: block;
    margin: 0 auto;
  }
}

