@media only screen and (max-width : 1199px) {
  
  .main-heading,
  .head-404 {
    font-size: 50px;
  }

  h1,
  h2 {
    font-size: 26px;
  }

  h3,
  .card-name,
  .modal-head {
    font-size: 22px;
  }

  h4,
  .subhead,
  .card-price,
  .adv-title,
  .contacts-title,
  .table-title,
  .sert-title,
  .prev-title,
  .vac-title,
  .gal-i-title,
  .grid-i-title {
    font-size: 18px;
  }
  .pre-heading {
    margin-bottom: 0;
  }
  .modal-subhead {
    font-size: 16px;
  }
  .slider-heading-wr {
    flex-wrap: wrap;
    h2 {
      margin: 20px 20px 20px 0;
    }
    h2 br  {
      display: none;
    }
  }

  .colorpickers-wr {
    flex-wrap: wrap;
  }
  .colorpickers-bar,
  .colorpick-area {
    flex-basis: 100%;
    min-width: 100%;
  }
  .colorpickers-bar {
    order: 2;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .colorpicker {
    flex: 1 0 240px;
  }
  .stage-i img,
  .stage-center {
    display: none;
  }
  .stage-i {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: left;
    width: 100%;
    padding-left: 40px;
    margin-bottom: 24px;
    &:before {
      content: "";
      position: absolute;
      top: 4px;
      
      left: 0;
      display: block;
      width: 24px;
      height: 24px;
      border-radius: 50%;
      background-color: #fff;
    }
    &:after {
      content: "";
      position: absolute;
      top: 8px;
      left: 4px;
      display: block;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      border: 4px solid #fff;
      background-color: #fff;
    }
    br {
      display: none;
    }
    h4 {
      margin-bottom: 8px;
    }
    .stage-text {
      text-align: left !important;
    }
  }
  .stage-i:nth-child(1):before,
  .stage-i:nth-child(1):after {
    background-color: #FFC928;
  }
  .stage-i:nth-child(2):before,
  .stage-i:nth-child(2):after {
    background-color: #FE891F;
  }
  .stage-i:nth-child(3):before,
  .stage-i:nth-child(3):after {
    background-color: #C04096;
  }
  .stage-i:nth-child(4):before,
  .stage-i:nth-child(4):after {
    background-color: #574A91;
  }
  .stage-i:nth-child(5):before,
  .stage-i:nth-child(5):after {
    background-color: #009CB0;
  }
  .stage-i:nth-child(6):before,
  .stage-i:nth-child(6):after {
    background-color: #92B940;
  }
  .stages-wr {
    min-height: 0;
  }
  .about-bar {
    top: 100px;
    width: 280px;
    font-size: 14px;
    z-index: 2;
  }
  .about-bar-content {
    padding: 20px 10px;
  }
  .about-head {
    font-size: 18px;
  }
  .about-bar p {
    padding-left: 0;
  }
  .about-bar .btn-wr {
    margin-bottom: 10px;
  }
  .footer-top {
    flex-wrap: wrap;
    >div {
      margin-bottom: 20px;
    }
  }
  .f-nav-wr {
    min-width: 60%;
  }
  .f-subhead {
    font-family: $bold;
    font-size: 16px;
  }
  .top-img {
    max-height: 410px;
  }

  .contacts-bar {
    flex-wrap: wrap;
    padding: 20px 10px;
  }
  .contacts-item {
    max-width: 50%;
    margin-bottom: 30px;
  } 




} /*max 1199*/

