@media only screen and (min-width : 1024px) {

  .scroll-wr {
    display: block;
    opacity: 0;
    position: relative;
    width: 20px;
    height: 32px;
    border-radius: 10px;
    border: 2px solid #333;
    margin: 20px auto 0;
    transition: all .4s;
    &.shown {
      opacity: 1;
    }
    span {
      position: absolute;
      top: 2px;
      left: 50%;
      transform: translateX(-50%);
      width: 2px;
      height: 6px;
      background-color: #333;
      animation: scroll 1s infinite;

    }
    i {
      position: absolute;
      top: 8px;
      width: 6px;
      height: 12px;
      background-image:url(../img/icons/arr-next.png);
      background-size: 100% 100%;
      background-repeat: no-repeat;
      animation: blink 1s infinite;
    }
    .l2 {
      left: -16px;
      transform: scale(-1, 1);
      animation-delay: -.5s;
      
    }
    .l1 {
      left: -12px;
      transform: scale(-1, 1);
    }
    .r1 {
      right: -12px;
    }
    .r2 {
      right: -16px;
      animation-delay: -.5s;
    }
  }

  @keyframes scroll {
    0% {
      opacity: 0;
      top: 2px;
    }
    50% {
      opacity: 1;
      top: 7px;
    }
    100% {
      opacity: 0;
      top: 12px;
    }
  }

  @keyframes blink {
    0% {
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

} /*min 1024*/


