/* ==================== common styles ======================= */

html {
  overflow-x: hidden;
}

body {
  min-width: 320px;
  overflow-x: hidden;
  color: $main;
  font-family: $reg;
  font-size: 18px;
  line-height: 1.5;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.hidden {
  display: none;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0, .8);
  z-index: 20;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  display: inline-block;
  background: none;
  padding: 0;
  border: none;
  outline: none;
  cursor: pointer;
  font-family: inherit;//tpl
  font-size: inherit;
  color: inherit;
  text-transform: inherit;


}

img {
  max-width: 100%;
}

input[type="file"] {
  display: none;
}

img,
input,
input:focus {
  outline: none;
  border: none;
}

::-webkit-input-placeholder {
  font-family: $reg;
  font-size: 18px;
  color: #848484;
  opacity: 1;
}

::-moz-placeholder {
  font-family: $reg;
  font-size: 18px;
  color: #848484;
  opacity: 1;
}

:-ms-input-placeholder {
  font-family: $reg;
  font-size: 18px;
  color: #848484;
  opacity: 1;
}

::placeholder {
  font-family: $reg;
  font-size: 18px;
  color: #848484;
  opacity: 1;
}

input,
textarea {
  &::-ms-clear {
    display: none;
  }
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: none;
}

select::-ms-expand {
    display: none;
}

textarea {
  display: block;
  resize: vertical;
  border: none;
  outline: none;

}

section {
  position: relative;
  padding: 80px 0 120px;
  li {
    &:before {
      content: "- ";
    }
  }
}

nav li:before {
  display: none;
}

p,
ul,
ol,
h4 {
  margin-bottom: 30px;
}

.wrapper {
  width: 96%;
  max-width: 1110px;
  margin: 0 auto;
  >img {
    margin: 40px 0 60px;
  }
}

.clearfix {
  display: table;
  width: 100%;
  clear: both;
}

.button {
  display: inline-block;
  position: relative;
  width: 350px;
  max-width: 96%;
  height: 68px;
  line-height: 60px;
  font-family: $bold;
  font-size: inherit;
  text-transform: uppercase;
  letter-spacing: 1px;
  
  color: #fff;
  white-space: nowrap;
  text-align: center;
  border: 4px solid $red;
  border-radius: 34px;
  background-color: $red;
  background-image:url(../img/edgeblur.png);
  background-size: 100% auto;
  background-position-y: 100%;
  transition: all 1.8s;
  padding: 0 8px;
  cursor: pointer;
  //overflow: hidden;
  &:before {
    content: "";
    display: block;
    width: 84%;
    height: 84%;
    border-radius: 24px;
    position: absolute;
    left: 8%;
    bottom: 0;
    box-shadow: 0px 15px 25px rgba(238,14,9,0);
    transition: all 1.5s;
    z-index: -1;
  }
  &:hover {
    color: $red;
    background-position-y: 0;
    &:before {
      box-shadow: 0px 15px 25px rgba(120,0,0,.5);
    }
  }
  &.small {
    width: 250px;
    height: 48px;
    line-height: 44px;
    border-radius: 26px;
    border-width: 2px;
    font-family: $med;
    font-size: 14px;
    &:before {
      border-radius: 20px;
      box-shadow: 0px 10px 15px rgba(120, 0, 0, 0);
    }
    &:hover {
      &:before {
        box-shadow: 0px 10px 15px rgba(120, 0, 0, .5);
      }
    }
  }
  &.white {
    color: $red;
    background-color: #fff;
    background-image:url(../img/edgeinverse.png);
    &:hover {
      color: #fff;
    }
  }
  &.transparent {
    background-color: transparent;
    border: 1px solid #646464;
    line-height: 46px;
    background-image:url(../img/edgetransp.png);
    &:hover {
      color: #333;
    }
    &:before {
      display: none;
    }
  }
}

.button.clear {
  background-color: transparent;
  background-image:url(../img/edgetranspred.png);
  color: $red;
  &:before {
    display: none;
  }
  &:hover {
    color: #fff;
  }
}


h1 {
  margin-bottom: 30px;
}


h1,
