.top-slider-wr {
  position: relative;
  //max-width: 1920px;
  margin: 0 auto;
  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    //background-color: rgba(0, 0, 0, 0.3);
    background-image: url(../img/pattern.png);
    background-repeat: repeat;
  }

}

.top-slider {
  opacity: 0;
}

.top-slider .slide img {
  width: 100%;
  margin: 0 auto;
}

.top-slider-dots {
  position: absolute;
  right: 115px;
  top: 50%;
  transform: translateY(-50%);
  
  .slick-dots {
    white-space: normal;
    li {
      display: block;
      width: 14px;

      height: 14px;
      border-radius: 50%;
      background-color: rgba(255,255,255,.5);
      margin-right: 0;
      margin-bottom: 15px; 
      transition: all .4s;
      cursor: pointer;
      border: 2px solid transparent;
      &:last-child {
        margin-bottom: 0; 
      }
      &.slick-active {
        background-color: transparent;
        border: 2px solid $red;
      }
      button {
        display: none;
      }
    }
  }
}

.slick-slide {
  outline: none;
}

