  font-size: .8em;
  color: #999;

}

.testim-r {
  flex-grow: 1;
  max-width: 740px;
}


.testimonials-section {
  .e1{bottom: 20%;}
  .e2{bottom: 10%;}
  .e3{top: 10%;}
  .e4{top: 20%;}
}

.testim-top-section {
  .top-img {
    left: 32%;
  }
}

.section-404 {
  font-family: $med;

  text-align: center;
  padding-bottom: 120px;
  &:after {
    display: none !important;
    
  }
  >.wrapper {
    justify-content: center;
  }
  .button {
    border-color: #fff;
    font-size: 18px;
    margin-top: 12px;
  }
  .button:hover:before {
    box-shadow: 0 15px 20px rgba(0,0,0,.9);
  }
}

.content-404 {
  position: relative;
  font-size: 24px;
  padding-top: 50px;
  z-index: 1;
}

.head-404 {
  font-family: $black;
  font-size: 72px;
  margin-bottom: 2px;
}

.head-404,
.section-404 p {
  min-width: 100%;
}

.img-404 {
  display: block;
  margin: 0 auto 16px !important;
}

.gallery-top-section .top-img {
  left: 45%;
}

.pagination {
  text-align: center;
  margin: 10px 0;
  white-space: nowrap;
  overflow: auto;

  a {
    display: inline-block;
    width: 50px;
    height: 50px;
    line-height: 48px;
    border-radius: 25px;
    border: 1px solid #ebebeb;
    font-family: $med;
    font-size: 14px;
    color: #3a3a3a;
    vertical-align: middle;
    margin: 0 5px 10px;
    cursor: pointer;
    transition: all 1.2s;
    &:hover,
    &.jp-current {
      background-color: $red;
      color: #fff;
      border-color: $red;
      transition: all .4s;
    }
  }
  .jp-previous,
  .jp-next {
    font-size: 0;
    width: 94px;
    border-radius: 25px;
    background-image:url(../img/icons/arr-r.png);
    background-position: 50% 50%;
    background-repeat: no-repeat;
    &:hover {
      background-image:url(../img/icons/arr-r-wh.png);
    }
  }
  .jp-previous {
    transform: scale(-1, 1);
  }
}


.grid-i-title,
.gal-i-title {
  display: block;
  font-family: $bold;
  line-height: 1.3;
  position: relative;

  &:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 65px;
    height: 8px;
    background-color: $red;
  }
}

.gal-i-title {
  padding: 18px 0 20px;
  margin-bottom: 30px;
}

.grid-i-title {
  padding: 30px 0 20px;
  margin-bottom: 30px;
}


.gallery-section {
  padding-bottom: 60px;
  .gal-i-title {
    margin-bottom: 0;
  }
}

.gallery-section .wrapper {
  position: relative;
  z-index: 3;
}


.video-section {
  padding: 120px 0;
  background-color: #e8e8e8;
}



.video-i iframe {
  width: 100%;
}


.video-section {
  .gal-i-title {
    &:after {
      display: none;
    }
  }
}

#paginationPage,
#paginationPage-6perPage, {
  align-content: flex-start;
}

