.products-top-section .top-img {
  bottom: auto;
  top: 0;
  left: 47%;
}

.divided {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 40px;
  text-align: center;
  >div,
  >a {
    display: block;
    flex: 1 1 290px;
    max-width: 380px;
    padding: 0 15px;
    margin-bottom: 30px;
  }
  img {
    margin-bottom: 30px;
  }
}



.products-section {
  .img-wr {
    background-color: #eee;
  }
}

.products-related-section {
  padding: 220px 0 100px;
  background-image:url(../img/lightsbg.jpg);
  background-size: cover;
  background-position: 50% 50%;
  .grid-i {
    position: relative;
    z-index: 1;
  }
}

.products-related-section,
.lights-catalog-section {
  .img-wr {
    background-color: $red;
  }
}

.grid-i.flex-col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.grid.centered {
  justify-content: center;
  .grid-i {
    margin-left: 15px;
    margin-right: 15px;
  }
  &:after {
    display: none;
  }
}

.lights-catalog-top-section {
  .top-img {
    left: 47%;
  }
}

.color-catalog-section,
.lights-catalog-section {
  .e1{bottom: 60px;}
  .e2{bottom: 200px;}
  .e3{top: 20px;}
  .e4{top: 150px;}
}

.color-catalog-section {
  .grid-i {
    margin-bottom: 50px;
  }
}

.color-section {
  h2, 
  h3, 
  h4, 
  h5, 
  p, 
  ul, 
  ol, 
  img {
    margin-bottom: 20px;
  }
  .float-block {
    padding-top: 60px;
  }
  .divided img {
    margin-bottom: 30px;
  }
}

