
/* ==================== fonts ======================= */



@font-face {
	font-family: "GothamPro-Regular";
	src: url("../fonts/Gotham/GothamPro.woff") format("truetype");
	font-style: normal;
	font-weight: 400;
}

@font-face {
	font-family: "GothamPro-Black";
	src: url("../fonts/Gotham/GothamPro-Black.woff") format("truetype");
	font-style: normal;
	font-weight: 900;
}

@font-face {
	font-family: "GothamPro-Bold";
	src: url("../fonts/Gotham/GothamPro-Bold.woff") format("truetype");
	font-style: normal;
	font-weight: 700;
}

@font-face {
	font-family: "GothamPro-Medium";
	src: url("../fonts/Gotham/GothamPro-Medium.woff") format("truetype");
	font-style: normal;
	font-weight: 600;
}

@font-face {
  font-family: "Rouble";
  src: url("../fonts/rouble/rouble.woff") format("woff");
  font-style: normal;
  font-weight: 700;
}


//variables
$reg: GothamPro-Regular, sans-serif;
$black: GothamPro-Black, sans-serif;
$bold: GothamPro-Bold, sans-serif;
$med: GothamPro-Medium, sans-serif;

$main: #0a0a0a;
$red: #ee0e09;
$redrgba: rgba(238,14,9,.2);

